/*** Miscellaneous ***/

html {
    overflow-x: hidden;
    height: 100%;
}

body {
    font-family: "Poppins", sans-serif;
    font-size: 15px;
    color: #fff;
    font-weight: 500;
    line-height: 1.6;
    position: relative;
    background-size: cover;
    background-position: center;
    height: 100%;
    width: 100%;
    background-color: #111;
}

.poppins-font {
    font-family: "Poppins", sans-serif;
}

.open-sans-font {
    font-family: "Open Sans", sans-serif;
}

img {
    max-width: 100%;
}

hr.separator {
    border-top: 1px solid #252525;
    margin: 70px auto 55px;
    max-width: 40%;
}

.ft-wt-600 {
    font-weight: 600;
}

.mb-30 {
    margin-bottom: 30px;
}

.pb-50 {
    padding-bottom: 50px;
}

.no-transform {
    transform: none !important;
}

.custom-title {
    padding-bottom: 22px;
    font-size: 26px;
}


