    }
    body.light .portfolio .slideshow nav span img {
        display: none;
    }
    body.light .portfolio .slideshow nav span.nav-next {
        // background-image: url("img/projects/navigation/right-arrow.png");
        background-size: cover;
    }
    body.light .portfolio .slideshow nav span.nav-prev {
        // background-image: url("img/projects/navigation/left-arrow.png");
        background-size: cover;
    }
    body.light .portfolio .slideshow nav span.nav-close {
        // background-image: url("img/projects/navigation/close-button.png");
        background-size: cover;
    }
    body.light .portfolio .slideshow nav span.nav-next,
    body.light .portfolio .slideshow nav span.nav-prev {
        top: 51px;
    }
    body.light .portfolio .slideshow {
        background: #fff;
    }
}

.custom-overlay.green {
    .icon {
        img {
            filter: invert(0);
        }
    }
}

.quotebox {
    position: relative;
    padding-left: 70px;
    margin-top: 35px;
    margin-bottom: 35px;
    .icon {
        position: absolute;
        left: 0;
        top: 10px;
        img {
            width: 40px;
            height: 40px;
            filter: invert(1);
        }
    }
    p {
        font-size: 17px;
        font-style: italic;
        margin-bottom: 23px;
    }
}

@media (max-width: 991px) {
    body.light {
        .header {
            background: #fff;
            box-shadow: 0 0px 47px #dedede;
            ul.icon-menu li.icon-box {
                background: #eee !important;
            }
        }
    }
}

@media (max-width: 576px) {
    body.light #menuToggle {
        border: 0;
    }
    body.light .portfolio .slideshow nav span.nav-next,
    body.light .portfolio .slideshow nav span.nav-prev {
        top: 31px;
    }
}


/*** Buttons ***/

.button {
    overflow: hidden;
    display: inline-block;
    line-height: 1.4;
    border-radius: 35px;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: center;
    cursor: pointer;
    vertical-align: middle;
    user-select: none;
    transition: 250ms all ease-in-out;
    text-transform: uppercase;
    text-decoration: none !important;
    position: relative;
    z-index: 1;
    padding: 16px 70px 16px 35px;
    font-size: 15px;
    font-weight: 600;
    color: #fff;
    background-color: transparent;
    outline: none !important;
    cursor: pointer !important;
}

.button .button-text {
    position: relative;
    z-index: 2;
    color: #fff;
    transition: 0.3s ease-out all;
}

.button:before {
    z-index: -1;
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateX(100%);
    transition: 0.3s ease-out all;
}

.button:hover .button-text {
    color: #ffffff;
}

.button:hover:before {
    transform: translateX(0);
}

.button-icon {
    position: absolute;
    right: -1px;
    top: -1px;
    bottom: 0;
    width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 19px;
    color: #ffffff;
    border-radius: 50%;
}


/*** Page Title ***/

.title-section {
    margin: 0 auto;
    width: 100%;
    position: relative;
    padding: 80px 0;
}

.title-section h1 {
    font-size: 56px;
    font-weight: 900 !important;
    color: #fff;
    text-transform: uppercase;
    margin: 0;
}

.title-bg {
    font-size: 110px;
    left: 0;
    letter-spacing: 10px;
    line-height: 0.7;
    position: absolute;
    right: 0;
    top: 50%;
    text-transform: uppercase;
    font-weight: 800;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    color: rgba(255, 255, 255, 0.07);
}


/* [ HEADER ] */


/*================================================== */


/*** Desktop Navigation ***/


/*** Navigation ***/

.home-details-container {
    @media (max-width: 991px) {
        // height: 100vh;
    }
}

.header {
    position: fixed;
    right: 30px;
    z-index: 3;
    display: flex;
    align-items: center;
    top: 50%;
    opacity: 1;
    transition: opacity 0.3s;
    -webkit-transition: opacity 0.3s;
    transform: translateY(-50%);
    z-index: 10;
    @media (max-width: 991px) {
        left: 0;
        right: auto;
        bottom: 0;
        top: auto;
        transform: none;
        width: 100%;
        display: block;
        background: #2b2a2a;
        padding: 8px;
        ul.icon-menu {
            display: flex;
            align-items: center;
            justify-content: space-around;
            li.icon-box {
                margin: 0 !important;
                width: 45px !important;
                height: 45px !important;
                background: #444 !important;
                i {
                    top: 14px;
                }
            }
            h2 {
                display: none;
            }
        }
    }
    @media (max-width: 360px) {
        ul.icon-menu {
            justify-content: space-between;
        }
    }
}

.header.hide-header {
    z-index: 0 !important;
    opacity: 0;
}

.header ul.icon-menu {
    margin: 0;
    padding: 0;
}

.header ul.icon-menu li.icon-box {
    width: 50px;
    height: 50px;
    list-style: none;
    position: relative;
    display: flex;
    align-items: center;
    transition: 0.3s;
    margin: 20px 0;
    border-radius: 50%;
    background: #2b2a2a;
    cursor: pointer;
}

.header ul.icon-menu li.icon-box i {
    color: #ddd;
    font-size: 19px;
    transition: 0.3s;
}

.header ul.icon-menu li.icon-box.react-tabs__tab--selected,
.header ul.icon-menu li.icon-box:hover,
.header ul.icon-menu li.icon-box.react-tabs__tab--selected i,
.header ul.icon-menu li.icon-box:hover i {
    color: #fff;
}

.header .icon-box h2 {
    font-size: 15px;
    font-weight: 500;
}

.header .icon-box i.fa {
    position: absolute;
    pointer-events: none;
}

.header .icon-menu i {
    left: 0;
    right: 0;
    margin: 0 auto;
    display: block;
    text-align: center;
    font-size: 35px;
    top: 15px;
}

.header .icon-box h2 {
    z-index: -1;
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0;
    color: #fff;
    line-height: 50px;
    font-weight: 500;
    transition: all 0.3s;
    border-radius: 30px;
    text-transform: uppercase;
    padding: 0 25px 0 30px;
    height: 50px;
    margin: 0;
}

.header .icon-box:hover h2 {
    opacity: 1;
    right: 27px;
    margin: 0;
    text-align: center;
    border-radius: 30px 0 0 30px;
}


/* [ Pages ] */


/*================================================== */


/*** Homepage ***/

.home .color-block {
    position: fixed;
    height: 200%;
    width: 100%;
    transform: rotate(-15deg);
    left: -83%;
    top: -50%;
}

.home .bg {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    height: calc(100vh - 80px);
    z-index: 11;
    border-radius: 30px;
    left: 40px;
    top: 40px;
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.9);
}

.home .container-home {
    display: flex;
    align-items: center;
    min-height: 100%;
}

.home .home-details>div {
    max-width: 550px;
    margin: 0 auto;
}

.home .home-details h1 {
    font-size: 51px;
    line-height: 62px;
    font-weight: 700;
    margin: 18px 0 10px;
    position: relative;
    padding-left: 70px;
}

.home .home-details h1:before {
    content: "";
    position: absolute;
    left: 0;
    top: 29px;
    height: 4px;
    width: 40px;
    border-radius: 10px;
}

.home .home-details h1 span {
    color: #fff;
    display: block;
}

.home .home-details h2 span {
    color: #fff;
    display: block;
}

.home .home-details h6 {
    color: #eee;
    font-size: 22px;
    font-weight: 400;
    display: block;
}

.home .home-details p {
    margin: 15px 0 28px;
    font-size: 16px;
    line-height: 35px;
}


/*** About ***/

.about .main-content {
    padding-bottom: 20px;
}


/* Personal Info List */

.about .about-list li:not(:last-child) {
    padding-bottom: 20px;
}

.about .about-list .title {
    opacity: 0.8;
    text-transform: capitalize;
}

.about .about-list .value {
    font-weight: 600;
}


/* Box Stats */

.about .box-stats {
    padding: 20px 30px 25px 40px;
    border-radius: 5px;
    border: 1px solid #252525;
}

.about .box-stats.with-margin {
    margin-bottom: 30px;
}

.about .box-stats h3 {
    position: relative;
    display: inline-block;
    margin: 0;
    font-size: 50px;
    font-weight: 700;
}

.about .box-stats h3:after {
    content: "+";
    position: absolute;
    right: -24px;
    font-size: 33px;
    font-weight: 300;
    top: 2px;
}

.about .box-stats p {
    padding-left: 45px;
}

.about .box-stats p:before {
    content: "";
    position: absolute;
    left: 0;
    top: 13px;
    width: 30px;
    height: 1px;
    background: #777;
}


/* Resume */

.about .resume-box ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.about .resume-box li {
    position: relative;
    padding: 0 20px 0 60px;
    margin: 0 0 50px;
}

.about .resume-box li:after {
    content: "";
    position: absolute;
    top: 0;
    left: 20px;
    bottom: 0;
    border-left: 1px solid #333;
}

.about .resume-box .icon {
    width: 40px;
    height: 40px;
    position: absolute;
    left: 0;
    right: 0;
    line-height: 40px;
    text-align: center;
    z-index: 1;
    border-radius: 50%;
    color: #fff;
    background-color: #252525;
}

.about .resume-box .time {
    color: #fff;
    font-size: 12px;
    padding: 1px 10px;
    display: inline-block;
    margin-bottom: 12px;
    border-radius: 20px;
    font-weight: 600;
    background-color: #252525;
    opacity: 0.8;
}

.about .resume-box h5 {
    font-size: 18px;
    margin: 7px 0 10px;
}

.about .resume-box p {
    margin: 0;
    color: #eee;
    font-size: 14px;
}

.about .resume-box .place {
    opacity: 0.8;
    font-weight: 600;
    font-size: 15px;
    position: relative;
    padding-left: 26px;
}

.about .resume-box .place:before {
    position: absolute;
    content: "";
    width: 10px;
    height: 2px;
    background-color: #fff;
    left: 7px;
    top: 9px;
    opacity: 0.8;
}


/* Skills */

.about .c100 {
    float: none !important;
    margin: 0 auto;
    background-color: #252525;
}

.about .c100>span {
    color: #fff;
}

.about .c100:after {
    background-color: #111;
}


/*** Portfolio ***/

.portfolio .main-content {
    padding-bottom: 60px;
    margin-top: -15px;
}


/* Grid */

.portfolio .grid-gallery ul {
    list-style: none;
    margin: 0 -15px;
    padding: 0;
}

.portfolio .grid-gallery figure {
    margin: 0;
}

.portfolio .grid-gallery figure img {
    display: block;
    width: 100%;
    border-radius: 5px;
    transition: 0.3s;
}

.portfolio .grid-wrap {
    margin: 0 auto;
}

.portfolio .grid {
    margin: 0 auto;
}

.portfolio .grid li {
    width: 33.333333%;
    float: left;
    cursor: pointer;
    padding: 15px;
}

.portfolio .grid figure {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    border-radius: 5px;
    padding: 0;
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    @media screen and (min-width: 1300px) {
        min-height: 229px;
        background: #ddd;
    }
    @media screen and (max-width: 450px) {
        min-height: 215px;
    }
}

.portfolio .grid figure img {
    display: block;
    position: relative;
}

.portfolio .grid li a {
    overflow: hidden;
}

.portfolio .grid li figure div span {
    margin: 0;
    text-transform: uppercase;
    color: #fff;
    font-size: 18px;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
    width: 100%;
}

.portfolio .slideshow figure {
    width: 100%;
    height: 100%;
    background: #252525;
    padding: 30px;
    border-radius: 10px;
    overflow: hidden;
    margin: 0;
    img {
        border-radius: 6px;
    }
    @media screen and (min-width: 1350px) {
        img {
            background: #ddd;
            min-height: 370px;
        }
    }
    @media screen and (max-width: 450px) {
        img {
            min-height: 166px;
        }
    }
}

.portfolio .slideshow a {
    text-decoration: underline;
}

.portfolio .slideshow figcaption {
    margin-bottom: 15px;
}

.portfolio .slideshow figcaption h3 {
    text-transform: uppercase;
    padding: 10px 0 20px;
    font-weight: 700;
    text-align: center;
    font-size: 33px;
}


/* Embedded Videos */

.portfolio .slideshow .videocontainer,
.portfolio .slideshow .responsive-video {
    max-width: 100%;
    border-radius: 4px;
    display: block;
    object-fit: cover;
}

.portfolio .slideshow .videocontainer {
    position: relative;
    padding-bottom: 56.2%;
    padding-top: 0;
    height: 0;
    max-width: 100%;
    overflow: hidden;
    width: 100%;
    display: block;
    margin: 0;
    border-radius: 4px;
}

.portfolio .slideshow .videocontainer iframe,
.portfolio .slideshow .videocontainer object,
.portfolio .slideshow .videocontainer embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
}


/* Carousel */

.portfolio .slideshow .carousel-indicators {
    bottom: 0;
    background: rgba(0, 0, 0, 0.4);
    padding: 10px 0;
    margin: 0;
}

.portfolio .slideshow .carousel-indicators li {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: 0 7px;
    background-color: #fff;
    opacity: 1;
}


/* Navigation */

.portfolio .slideshow nav span {
    position: fixed;
    z-index: 1000;
    text-align: center;
    cursor: pointer;
    padding: 200px 30px;
    color: #fff;
}

.portfolio .slideshow nav span.nav-prev,
.portfolio .slideshow nav span.nav-next {
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    font-size: 41px;
    line-height: 58px;
}

.portfolio .slideshow nav span.nav-prev {
    left: 0;
}

.portfolio .slideshow nav span.nav-next {
    right: 0;
}

.portfolio .slideshow nav span.nav-close {
    top: 30px;
    right: 30px;
    padding: 0;
}


/*** Contact ***/

.contact .main-content {
    padding-bottom: 85px;
}


/* Left Side */

.contact .custom-span-contact {
    font-weight: 600;
    padding-left: 50px;
    line-height: 25px;
    padding-top: 5px;
    margin-bottom: 20px;
}

.contact .custom-span-contact i {
    left: 0;
    top: 10px;
    font-size: 33px;
}

.contact .custom-span-contact i.fa-phone-square {
    font-size: 39px;
    top: 7px;
}

.contact .custom-span-contact span {
    text-transform: uppercase;
    opacity: 0.8;
    font-weight: 400;
    padding-bottom: 0;
}

.contact .custom-span-contact a {
    color: #fff;
    text-decoration: none;
    cursor: pointer !important;
}

body.light .contact .custom-span-contact a {
    color: #666;
}

.contact ul.social {
    margin-left: -5px;
    padding-top: 15px !important;
}

.contact ul.social li {
    display: inline-block;
}

.contact ul.social li a {
    display: inline-block;
    height: 40px;
    width: 40px;
    line-height: 42px;
    text-align: center;
    color: #fff;
    transition: 0.3s;
    font-size: 17px;
    margin: 0 6px;
    background: #2b2a2a;
    border-radius: 50%;
}


/* Form */

.contact .contactform input[type="text"],
.contact .contactform input[type="email"] {
    border: 1px solid #111;
    width: 100%;
    background: #252525;
    color: #fff;
    padding: 11px 26px;
    border-radius: 30px;
    outline: none !important;
    transition: 0.3s;
}

